<template>
  <transition
    appear
    enter-active-class="animateTopLeft-enter"
    leave-active-class="animateTopLeft-leave"
    :duration="250"
  >
    <div v-if="shown" :class="['FirstTimeHereCorner']" :style="styling">
      <div class="circle" />
      <div class="text" :style="textStyling">First time here ?</div>
      <button @click="close()">Close</button>
    </div>
  </transition>
</template>

<script>
export default {
  name: 'FirstTimeHereCorner',
  props: {
    settings: {
      type: Object,
      required: true
    }
  },
  data: () => ({
    shown: false
  }),
  computed: {
    styling() {
      return {
        top: 0,
        left: 0,
        width: '25rem',
        height: '25rem'
      }
    },
    textStyling() {
      return {
        color: '#FFFFFF',
        'z-index': '999'
      }
    }
  },
  mounted() {
    setTimeout(() => {
      this.shown = true
    }, 250)
  },
  methods: {
    close() {
      this.closing = true

      setTimeout(() => {
        this.shown = false
        this.closing = false
      }, 250)
    }
  }
}
</script>

<style lang="scss" scoped>
.FirstTimeHereCorner {
  position: fixed;
  z-index: 999999;

  .text {
    position: relative;
    margin-left: 2rem;
    margin-top: 2rem;
  }

  .circle {
    z-index: 0;
    background-color: $oxford-blue;
    border: 3px solid $azure-radiance;
    position: absolute;
    left: -25%;
    top: -25%;
    width: 100%;
    height: 100%;
    border-radius: 100%;
  }

  button {
    position: relative;
    margin-left: 2rem;
  }
}
</style>

<style lang="scss">
@function map-deep-get($map, $keys...) {
  @each $key in $keys {
    $map: map-get($map, $key);
  }
  @return $map;
}

$positions: (
  'TopLeft': (
    'transformLeft': 0%,
    'transformRight': 0%
  ),
  'TopRight': (
    'transformLeft': 0%,
    'transformRight': 100%
  ),
  'BottomLeft': (
    'transformLeft': 100%,
    'transformRight': 0%
  ),
  'BottomRight': (
    'transformLeft': 100%,
    'transformRight': 100%
  )
);

@each $position, $value in $positions {
  .animate#{$position}-enter {
    animation: scaleUp#{$position}
      0.25s
      cubic-bezier(0.39, 0.575, 0.565, 1)
      both;
  }

  .animate#{$position}-leave {
    animation: scaleDown#{$position}
      0.25s
      cubic-bezier(0.39, 0.575, 0.565, 1)
      both;
  }

  @keyframes scaleUp#{$position} {
    0% {
      -webkit-transform: scale(0.5);
      transform: scale(0.5);
      -webkit-transform-origin: map-deep-get(
          $positions,
          #{$position},
          'transformLeft'
        )
        map-deep-get($positions, #{$position}, 'transformRight');
      transform-origin: map-deep-get($positions, #{$position}, 'transformLeft')
        map-deep-get($positions, #{$position}, 'transformRight');
    }
    100% {
      -webkit-transform: scale(1);
      transform: scale(1);
      -webkit-transform-origin: map-deep-get(
          $positions,
          #{$position},
          'transformLeft'
        )
        map-deep-get($positions, #{$position}, 'transformRight');
      transform-origin: map-deep-get($positions, #{$position}, 'transformLeft')
        map-deep-get($positions, #{$position}, 'transformRight');
    }
  }

  @keyframes scaleDown#{$position} {
    0% {
      -webkit-transform: scale(1);
      transform: scale(0.5);
      -webkit-transform-origin: map-deep-get(
          $positions,
          #{$position},
          'transformLeft'
        )
        map-deep-get($positions, #{$position}, 'transformRight');
      transform-origin: map-deep-get($positions, #{$position}, 'transformLeft')
        map-deep-get($positions, #{$position}, 'transformRight');
    }
    100% {
      -webkit-transform: scale(1);
      transform: scale(0.5);
      -webkit-transform-origin: map-deep-get(
          $positions,
          #{$position},
          'transformLeft'
        )
        map-deep-get($positions, #{$position}, 'transformRight');
      transform-origin: map-deep-get($positions, #{$position}, 'transformLeft')
        map-deep-get($positions, #{$position}, 'transformRight');
    }
  }
}
</style>
